
import $ from "jquery";
import XTable from 'vux/src/components/x-table/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
data() {
return {
isChange: false,
startDate: '',
endDate: '',
visibility: false,
visibility2: false,
widths: 100,
tableHead: ["无聊名称", "规格", "单位", "数量", "价格单位", '不含税金额', '折扣', '折后不含税金额', '折后不含税金额']
};
},
components: {
XTable,
Datetime,
Group
},





mounted() {
this.widths = 850 + "px";
console.log($(window).width());
this.$nextTick(() => {
this.init();
});

},
methods: {
toInfo() {
this.$router.push({path: '/orderInfo'})
},
change() {

if (this.widths === '850px') {
this.isChange = true
} else {
this.isChange = false
this.widths = '850px'
}
},
init() {
var win = $(window),
scrollAreaEl = $(".t_r_content"),
leftFreezeEl = $(".t_l_freeze"),
leftTableEl = leftFreezeEl.find("table"),
rightTableEl = $(".t_r_t table");


function adjustHeight() {
var winHeight = win.height(),
tableHeight = 10 * 40;
leftFreezeEl.height(tableHeight);
scrollAreaEl.height(tableHeight);
}

adjustHeight();
win.on("resize", adjustHeight);


var myScroll = new IScroll(".t_r_content", {
scrollX: true,
scrollY: true,
probeType: 3
});


scrollAreaEl.on("touchmove mousewheel", function(e) {
e.preventDefault();
});


myScroll.on("scroll", updatePosition);
myScroll.on("scrollEnd", updatePosition);

function updatePosition() {
var a = this.y;
var b = this.x;
leftTableEl.css(
"transform",
"translate(0px, " + a + "px) translateZ(0px)"
);
rightTableEl.css(
"transform",
"translate(" + b + "px, 0px) translateZ(0px)"
);
}
}
}
};



